    -webkit-transform:scale(1,1);
        -ms-transform:scale(1,1);
            transform:scale(1,1);
  }
  .pageWrapper{
    width:100%;
    height:100%;
    min-width: 80%;
    max-width: 100%;
    width:420px;
    margin:auto;
  }

  .workPage{
    min-width: 200px;
  }

  .skillTitle:nth-child(1),.skillTitle:nth-child(2){
    border-right:none;
  }
}

@media (max-height:800px){
  .nav{
    -webkit-animation:none;
            animation:none;
  }

  .toptxt{
    width:150%;
  }
  .toptxt h1, .toptxt h2{
    width:100%;
    line-height:1.7em;
  }
}



